/*Bienvenida y Navbar*/
.bienvenida {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-content: center;
    align-items: center;

    background-color: white;

    width: 90%;
    margin: auto;
    padding: 20px;

    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.bienvenida-tituloPrincipal {
    font-size: 40px;
    font-style: italic;
    padding: 10px;
}

.bienvenida-parrafoPrincipal {
    font-size: 18px;
    font-style: italic;
    padding: 10px;
}

.navbar{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    background-color: white;
    width: 90%;
    margin: auto;
    margin-bottom: 30px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.navbar-element{
    padding: 20px 40px 20px 40px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    transition: all 1s;
}
.navbar-element:hover{
    color: #EBA272;
    background-color: black;
    box-shadow: inset 0 0 0 15px #EBA272;
}

/*Carrito de Compras*/
.carrito{
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;

    width: 90%;
    margin: auto;
    padding: 10px;

    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}